import React, { useState } from 'react';
import { Link } from 'gatsby'
import { window } from 'browser-monads';

const AtelierCard = props => {
    const [hover, setHover] = useState(false)

    const handleHover = () => {
        setHover(!hover)
    }

    const styles = {
        open: {
            maxHeight: hover ? '1000px' : window.location.pathname === '/subscription' ? '1000px' : 0,
            opacity: hover ? 1 : window.location.pathname === '/subscription' ? 1 : 0,
            transition: '500ms all'
        },
        color: {
            color: hover ? '#e7205b' : window.location.pathname === '/subscription' ? '#e7205b' : ''
        },
        button: {
            backgroundColor: hover ? '#000' : ''
        }
    }

    return (
        <div className="atelier__card atelier" onMouseEnter={handleHover} onMouseLeave={handleHover}>
            <div className="atelier__card__content">
                <div>
                    {props.atelier.Date.split(' ').map((e, i) => (
                        <p className={`atelier__date date date${i}`} >{e}</p>
                    ))}
                </div>
                <div className="atelier__card__mainContent">
                    <p className="atelier__type type"> {props.atelier.type}</p>
                    <p className="atelier__title title" style={styles.color}> {props.atelier.titre}</p>
                    <p className="atelier__description" style={styles.open}>{props.atelier.description}</p>
                </div>
            </div>
            {window.location.pathname !== '/subscription' &&
                <div className="atelier__card__buttons">
                    <Link to={`/atelier/${props.atelier.id}`} style={styles.button} > DÃ©tails
                        </Link>
                    <Link to={`/subscription`} state={{ atelier: props.atelier }} style={styles.button}> S'inscrire
                    </Link>
                </div>
            }
        </div>
    )
}

export default AtelierCard;
